
.login-wrap {
   display: table-cell;
   text-align: center;
   vertical-align: middle;
}


.form-signin {
    min-height: 200px;
    width: 100%;
    max-width: 360px;
    min-height: 220px;
    margin: 0 auto;
    position: relative;
    padding: 20px;
    padding-top: 34px;
    background: #f5ead9;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.3);
}

.form-signin button {
    background:none;
    border:none;
    box-shadow:none;
}

.form-signin button:hover {
    background:none;
    border:none;
    box-shadow:none;
}

.form-signin .large {
    color: #333;
    font-size: 24px;
    font-weight: bold;
}

.form-signin input[type=text],
.form-signin input[type=email],
.form-signin input[type=password],
.create textarea {
    color: #a69f95;
    border: 1px solid #d7cebf;
    width: 100%;
    resize: none;
    background-color: #f5ead9;
    margin-bottom: 5px;
}
